  display: inline;
}

.direct {
  /* background: url(./images/chat-bubble.png) no-repeat 50%; */
  min-height: 247px;
  position: relative;
  margin-top: 50px;
}

.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto
}

@media (min-width:576px) {
  .container {
    max-width: 540px
  }
}

@media (min-width:768px) {
  .container {
    max-width: 720px
  }
}

@media (min-width:992px) {
  .container {
    max-width: 960px
  }
}

@media (min-width:1200px) {
  .container {
    max-width: 1140px
  }
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.lp-slideshow-gallery,
.lp-slideshow-gallery * {
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

.lp-slideshow-gallery {
  width: 100%;
  margin: 2px;
}

.lp-slideshow-gallery>.slide-container {
  position: relative;
  margin: auto;
  width: 100%;
}

.lp-slideshow-gallery>.slide-container>.slide {
  width: 100%;
  height: 100%;
  position: absolute;
  margin: auto;
  opacity: 0;
  transition: opacity 0.6s ease;
  -webkit-transition: opacity 0.6s ease;
  -moz-transition: opacity 0.6s ease;
  -o-transition: opacity 0.6s ease;
}

.lp-slideshow-gallery>.slide-container>.slide.active {
  opacity: 1;
}

.lp-slideshow-gallery>.slide-container>.slide>.image {
  width: 100%;
  height: 100%;
  object-fit: cover
}

.lp-slideshow-gallery>.slide-container>.slide>.number-text {
  color: #fff;
  font-size: 1rem;
  padding: 8px 12px;
  position: absolute;
  top: 0;
}

.lp-slideshow-gallery>.slide-container>.slide>.caption-text {
  color: #fff;
  font-size: 1.3rem;
  padding: 8px 12px;
  position: absolute;
  bottom: 0px;
  width: 100%;
  text-align: center;
  background-color: rgba(0, 0, 0, 0.2);
}

.lp-slideshow-gallery>.slide-container>.prev,
.lp-slideshow-gallery>.slide-container>.next {
  cursor: pointer;
  position: absolute;
  top: 50%;
  width: auto;
  margin-top: -22px;
  padding: 16px;
  font-size: 18px;
  color: #fff;
  font-weight: bold;
  transition: background-color 0.6s ease;
  -webkit-transition: background-color 0.6s ease;
  -moz-transition: background-color 0.6s ease;
  -o-transition: background-color 0.6s ease;
}

.lp-slideshow-gallery:hover>.slide-container>.prev,
.lp-slideshow-gallery:hover>.slide-container>.next {
  background-color: rgba(0, 0, 0, 0.2);
}

.lp-slideshow-gallery:hover>.slide-container>.prev:hover,
.lp-slideshow-gallery:hover>.slide-container>.next:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

.lp-slideshow-gallery>.slide-container>.next {
  right: 0;
  border-radius: 3px 0 0 3px;
}

.lp-slideshow-gallery>.slide-container>.prev {
  left: 0;
  border-radius: 0 3px 3px 0;
}

